import React, { Component } from 'react'
import {Link, withRouter} from 'react-router-dom'
import './Equipment.css'
import axios from 'axios';
 import authService from '../../services/auth-service';
 import { API_URL } from '../../services/url';
export class Equipment extends Component {
    constructor(props) {
        super(props)
        this.state = {
          subcategory : "",
          company : localStorage.getItem('companyId'),
          equipmentStatus : "1",
          location :"",
          pinCode : "",
          userdata  :[],
          subcatdataonclick : [],
          quantity: '1',
          capacity:''
        }
    }
    componentDidMount() {
      this.siteclick();
  }
  handleclick() {        
   axios.get(`${API_URL}/category/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
   .then(async response => {
       console.log(response)
           console.log(response.data);
           const data = response.data;
          await this.setState({ userdata : data })
           }).catch(error => {
           console.log(error.response); 
           })
    }
 siteclick ()//= e =>  
 {
   console.log(this.state.category);
   const sitemeet = this.state.category;
   console.log('sitemeet id')
   console.log(sitemeet)
   axios.get(`${API_URL}/subcategory/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
   .then(async response => {
     console.log(response)
         const data = response.data;
         await this.setState({ subcatdataonclick : data })
         console.log('subcatdataonclick')
         console.log(this.state.subcatdataonclick)
         }).catch(error => {
         console.log(error.response); 
         })
   }
    change = e => {
        this.setState ({
            [e.target.name] : e.target.value,
            
        })
    }
        onSubmit = e => {
          if(this.state.quantity<100){
            if(this.state.pinCode.length===6 && this.state.pinCode >99999){
            e.preventDefault();
            console.log(this.state);
            console.log('hi')
            axios.post(`${API_URL}/equipment/save`, this.state,axios.defaults.headers.common['w_auth'] = authService.getToken())
        .then(function(response){ 
           console.log(response);
           console.log(response.status)
           if(response.status === 200)
           {
             alert('Equipment Has Been Added.')
             this.props.history.push('/ViewEquipment')
           }
        }.bind(this))
        .catch(error => {
            console.log(error.response);
          })}else{
            alert("Pincode should be 6 digit");
          }
        }else{
          alert("Maximum Quantity Limit is 100 ");
        }

        }

  render() {
    return (
        <React.Fragment>
      <div>
        <main className="main">
        <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/"}>Home</Link></li>
          <li class="breadcrumb-item">
            Add Equipment
          </li>
          </ol>
            <div className="container-fluid">
                <div className="animated fadeIn">
                    <div className="row">
                       <div className="col-sm-12">
                       <div className="card custom-equipment-card">
                  <div className="card-header">
                    <strong>Equipment </strong>
                    <small>&nbsp;&nbsp;Form</small>
                  </div>
                  <div className="card-body">
                    <form autoComplete = "OFF">
                    <div className="row">
                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="ccnumber">Equipment Name*</label>
                          <select className="form-control" name="subcategory" value={this.state.subcategory}  onChange={(e) => this.setState({subcategory: e.target.value})}>
                                  <option>choose....</option>
                          {
                                    this.state.subcatdataonclick ?
                                    this.state.subcatdataonclick.map(function(item, id) {
                                      return(
                                          
                              <option key ={id} value={item.id}>{item.name}</option>
                            )
                                    }
                            )
                                    :
              <span>Data is loading....</span>
            }

    </select>
                        </div>
                        
                      </div>
                      <div className="col-sm-3">
                        <div className="form-group">
                          <label for="name">Enter Quantity</label>
                          <input className="form-control" id="quantity" type="number" placeholder="Enter quantity" name="quantity" value = {this.state.quantity} onChange = {e => this.change(e)} max="100"/>
                        <small>Enter Number Only</small>
                        </div>
                            </div>
                            <div className="col-sm-3">
                        <div className="form-group">
                          <label for="name">Enter Capacity</label>
                          <input className="form-control" id="quantity" type="text" placeholder="Enter Capacity" name="capacity" value = {this.state.capacity} onChange = {e => this.change(e)} max="100"/>
                        {/* <small>Enter Number Only</small> */}
                        </div>
                      </div>
                      
                     <div className="col-md-6">
                        <div className="form-group">
                          <label for="name">Enter Location*</label>
                          <input className="form-control" id="name" type="text" placeholder="Enter Location" name="location" value = {this.state.location} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                      <div className="col-md-6">
                        <div className="form-group">
                          <label for="name">Enter Pincode*</label>
                          <input className="form-control" id="name" type="number" placeholder="Enter Pincode" name="pinCode" value = {this.state.pinCode} onChange = {e => this.change(e)} min="99999" max="999999"/>
                        <small>Enter Number Only</small>
                        </div>
                      </div>
                    </div>
                        <br />

                   <div className="row">
                        <div className="col-sm-12 text-center center-block">
                            <button type="button" className="btn btn-primary" onClick = {e => this.onSubmit(e)}>Submit</button>
                        </div>
                   </div>
                   </form>
                  </div>
                </div>   
                       </div>     
                    </div>
                </div>
            </div>
        </main>
      </div>
      </React.Fragment>
    )
  }
}
export default withRouter(Equipment)
