import React, { Component } from 'react'
import './Requirement.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
import authService from '../../services/auth-service';
import { API_URL } from '../../services/url';
export class ViewRequirement extends Component {
  constructor(props)
  {
      super(props)
      this.state = {
        userdata : []
      }
      
  }
  componentDidMount() {
      this.handleclick();
  }
  handleclick() {        
    axios.get(`${API_URL}/requirement/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
    .then(async response => {
        console.log(response)
            console.log(response.data);
            const data = response.data;
            await this.setState({ userdata : data })
            console.log('userdata')
            console.log(this.state.userdata)
            }).catch(error => {
            console.log(error.response); 
            })
   }
  

render() {
  let sr=0;
   let requirementdata;
   if(this.state.userdata.length === 0)
   {
     requirementdata = (
       <div>
         <center><h4><strong>There is No Requirement Added Yet.</strong></h4></center>
         </div>
     )
   }
   else
   {
     requirementdata = (
       <div>
          <table className="table table-responsive-sm table-bordered">
   <thead className="thead-dark">
     <tr>
        <th scope="col">Sr. No.</th>
       {/*<th scope="col">Category</th> */}
       <th scope="col">Equipment Name</th>
       <th scope="col">Capacity</th>
       <th scope="col">Location</th>
       <th scope="col">Pincode</th>
       <th scope="col">Tenure(Days)</th>
       <th scope="col">Date of Creation</th>
     </tr>
   </thead>
   <tbody>
   {
           this.state.userdata ?
           this.state.userdata.map(function(item, _id) {
             return(
                 
     <tr key = {_id}>
        <td>{++sr}</td>
       {/*<td>{item.category.name || "NO DATA"}</td> */}
       <td>{item.subcategory.name || "NO DATA"}</td>
       <td>{item.capacity || "NO DATA"}</td>
       <td>{item.location || "NO DATA"}</td>
       <td>{item.pinCode || "NO DATA"}</td>
       <td>{item.tenure || "NO DATA"} Days</td>
       <td>{new Date(item.createdAt).toString().split(' ').slice(0, 4).join(' ')
        || "NO DATA"}</td>
             </tr>
   )
           }, this
   )
           :
           <span>No Data Present....</span>
         }
   </tbody>
 </table>

       </div>
     )
   }
 return (
     <React.Fragment>
   <div> 
         <main className="main">
         <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/"}>Home</Link></li>
          <li class="breadcrumb-item">
            View Requirement
          </li>
          </ol>
             <div className="container-fluid">
                 <div className="row">
                    
                     <div className="col-sm-12">
                     <div className="card custom-equipment-card-view">
                     <div className="row">
                         <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                           <Link to ={process.env.PUBLIC_URL +"/Requirement"}> <button type="button" className="btn btn-info custom-add-btn"><i className="icon-plus"></i>&nbsp;&nbsp;Add Requirement</button> </Link>
                         <br /><br/>
                     </div>
                         </div>
               {/* <div className="card-header table-bordered">
                 <i className="fa fa-align-justify"></i> Requirements</div> */}
               <div className="card-body custom-card-padding">
              
                {requirementdata}
                
               </div>
             </div>
                     </div>
                 </div>
             </div>
         </main>
   </div>
   </React.Fragment>
 )
}
}
export default ViewRequirement
