import React, { Component } from 'react'
import './Requirement.css'
import {Link} from 'react-router-dom'
import axios from 'axios';
 import authService from '../../services/auth-service';
 import { API_URL } from '../../services/url';
export class Requirement extends Component {
  constructor(props) {
    super(props)
    this.state = {
      subcategory : "",
      company : "",
      location : "",
      pinCode : "",
      requirementStatus : "1",
      userdata  :[],
      subcatdataonclick : [],
      tenure:'',
      capacity:''
    }
}
componentDidMount() {
  this.siteclick();
}
handleclick() {        
axios.get(`${API_URL}/category/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
.then(async response => {
   console.log(response)
       console.log(response.data);
       const data = response.data;
      await this.setState({ userdata : data })
       console.log('userdata')
       console.log(this.state.userdata)
       }).catch(error => {
       console.log(error.response); 
       })

}
siteclick () {

console.log(this.state.category);
// console.log(this.state.value);
const sitemeet = this.state.category;
console.log('sitemeet id')
console.log(sitemeet)
axios.get(`${API_URL}/subcategory/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
.then(async response => {
console.log(response)
     const data = response.data;
     await this.setState({ subcatdataonclick : data })
     console.log('subcatdataonclick')
     console.log(this.state.subcatdataonclick)
     }).catch(error => {
     console.log(error.response); 
     })
}
change = e => {
    this.setState ({
        [e.target.name] : e.target.value,
        company : localStorage.getItem('companyId')
    })
}
    onSubmit = e => {
      if(this.state.pinCode.length===6 && this.state.pinCode >99999 ){
        if(this.state.tenure<=1000 && this.state.tenure > 0){
        e.preventDefault();
        console.log(this.state);
        console.log('hi')
        axios.post(`${API_URL}/requirement/save`, this.state,axios.defaults.headers.common['w_auth'] = authService.getToken())
        .then(function(response){ 
          console.log(response);
          console.log(response.status)
          if(response.status === 200)
          {
            alert('Requirement Has Been Added.')
            this.props.history.push('/ViewRequirement')
          }
        }.bind(this))
        .catch(error => {
            console.log(error.response);
          })
        }else{
          alert("Maximum tenure days limit 1000 days, Please insert value less than 1000 ")
        }
        }else{
          alert("Pincode should be 6 digits");
        }
  }

render() {
return (
    <React.Fragment>
  <div>
    <main className="main">
    <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/"}>Home</Link></li>
          <li class="breadcrumb-item">
            Add Requirement
          </li>
          </ol>
        <div className="container-fluid">
            <div className="animated fadeIn">
                <div className="row">
                   <div className="col-sm-12">
                   <div className="card custom-equipment-card">
              <div className="card-header">
                <strong>Requirement </strong>
                <small>&nbsp;&nbsp;Form</small>
              </div>
              <div className="card-body">
                <form autoComplete = "OFF">
                <div className="row">
                  <div className="col-sm-6">
                    <div className="form-group">
                      <label for="ccnumber">Equipment Name*</label>
                      <select className="form-control" name="subcategory" value={this.state.subcategory}  onChange={(e) => this.setState({subcategory: e.target.value})}>
                            <option>choose....</option>
                          {
                                    this.state.subcatdataonclick ?
                                    this.state.subcatdataonclick.map(function(item, id) {
                                      return(
                                          
                              <option key ={id} value={item.id}>{item.name}</option>
                            )
                                    }
                            )
                                    :
                                    <span>Data is loading....</span>
                                  }

                          </select>
                    </div>
                  </div>
                  <div className="col-sm-6">
                        <div className="form-group">
                          <label for="name">Enter Capacity*</label>
                          <input className="form-control" id="capacity" type="number" placeholder="Enter capacity" name="capacity" value = {this.state.capacity} onChange = {e => this.change(e)} min="99999" max="999999"/>
                        <small>Enter Number Only</small>
                        </div>
                      </div>
                  
                  <div className="col-sm-12">
                    <div className="form-group">
                      <label for="name">Tenure(Days)*</label>
                      <input className="form-control" id="tenure" type="number" placeholder="tenure" name="tenure" value = {this.state.tenure} onChange = {e => this.change(e)} max="1000"/>
                    </div>
                  </div>
                </div>
                              
                <div className="row">
                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="name">Enter Location*</label>
                          <input className="form-control" id="name" type="text" placeholder="Enter Location" name="location" value = {this.state.location} onChange = {e => this.change(e)}/>
                        </div>
                      </div>

                      <div className="col-sm-6">
                        <div className="form-group">
                          <label for="name">Enter Pincode*</label>
                          <input className="form-control" id="name" type="number" placeholder="Enter Pincode" name="pinCode" value = {this.state.pinCode} onChange = {e => this.change(e)} min="99999" max="999999"/>
                        <small>Enter Number Only</small>
                        </div>
                      </div>
                    </div>
                    <br />

               <div className="row">
                    <div className="col-sm-12 text-center center-block">
                        <button type="button" className="btn btn-primary" onClick = {e => this.onSubmit(e)}>Submit</button>
                    </div>
               </div>
               </form>
              </div>
            </div>   
                   </div>     
                </div>
            </div>
        </div>
    </main>
  </div>
  </React.Fragment>
)
}
}

export default Requirement
