import React, { Component } from 'react'
import './Equipment.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
import authService from '../../services/auth-service';
import { API_URL } from '../../services/url';
export class ViewEquipment extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
              userdata : []
        }
        this.deletehandler = this.deletehandler.bind(this)
        
    }
    componentDidMount() {
        this.handleclick();
    }
    handleclick() {        
      axios.get(`${API_URL}/equipment/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
      .then(async response => {
          console.log(response)
          const data = response.data;
        await this.setState({ userdata: data });
          console.log('userdata')
          console.log(this.state.userdata)
          }).catch(error => {
            console.log(error); 
          })

    }
    async deletehandler (item)
    {
     await this.setState({_id:item._id,
      });
      console.log(this.state._id);
      const response = await axios.delete(`${API_URL}/equipment/delete`,{ _id: item._id } , axios.defaults.headers.common['w_auth'] = authService.getToken() )
        console.log(response)
        if (response.data.n===1){
          alert("Equipment has been deleted");
          this.handleClick();
        }
    }
    

 render() {
   let sr=0;
   let equipmentdata;
   if(this.state.userdata.length === 0)
   {
     equipmentdata = (
       <div>

         <center><h4><strong>There is No Equipment Yet.</strong></h4></center>
       </div>
     )
   }
   else{
     equipmentdata = (
       
       <div>
          <table className="table table-responsive-sm table-bordered">
     <thead className="thead-dark">
       <tr>
         <th scope="col">Sr. No</th>
               <th scope="col">Equipment Name</th>
               <th scope="col">Capacity</th>
         <th scope="col">Quantity</th>
         <th scope="col">Location</th>
         <th scope="col">Pin Code</th>
         <th scope="col">Action</th>
       </tr>
     </thead>
     <tbody>
     {
      
             this.state.userdata ?
             this.state.userdata.map(function(item, _id) {
               return(
                   
       <tr key = {_id}>
         {/* {/* <td>{item.name || "NO DATA"}</td> */}
         <td>{++sr }</td> 
                   <td>{item.subcategory.name || "NO DATA"}</td>
                   <td>{item.capacity || "Unknown"}</td>
         <td>{item.quantity || "1"}</td>
        <td>{item.location || "NO DATA"}</td>
        <td>{item.pinCode || "NO DATA"}</td>
        <td>
        {/* <button type="button" className="btn btn-warning custom-edit-btn custom-btn" onClick = {this.edithandler.bind(this)}><i className="icon-pencil icons"></i></button> */}
        <button type="button" className="btn btn-danger custom-btn" onClick = {this.deletehandler.bind(this,item)}><i className="icon-trash icons"></i></button>
        </td>
               </tr>
     )
             }, this
     )
             :
             <span>No Data Present....</span>
           }
     </tbody>
   </table>
       </div>
     )
   }
   return (
       <React.Fragment>
     <div> 
           <main className="main">
           <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/"}>Home</Link></li>
          <li class="breadcrumb-item">
            View Equipment
          </li>
          </ol>
               <div className="container-fluid">
                   <div className="row">
                      
                       <div className="col-sm-12">
                       <div className="card custom-equipment-card-view">
                       <div className="row">
                           <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                             <Link to ={process.env.PUBLIC_URL +"/Equipment"}> <button type="button" className="btn btn-info custom-add-btn"><i className="icon-plus"></i>&nbsp;&nbsp;Add Equipment</button> </Link>
                           <br /><br/>
                       </div>
                           </div>
                 <div className="card-body custom-card-padding">
                 {equipmentdata}
                 </div>
               </div>
                       </div>
                   </div>
               </div>
           </main>
     </div>
     </React.Fragment>
   )
 }
}
export default ViewEquipment
